'use client';
import React, { useState, useEffect, useRef } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Menu, X, Phone, Mail, Mountain, Flag, Binoculars, ChevronDown } from 'lucide-react';
import { useTheme } from '../contexts/ThemeContext';
import { getImageSrc } from '../utils/imageHelpers';
import lightImage from '../assets/images/logo-light.png';
import darkImage from '../assets/images/logo-dark.png';
import ThemeToggle from './ThemeToggle';
import BookingModal from './BookingModal';

const Header: React.FC = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isBookingModalOpen, setIsBookingModalOpen] = useState(false);
  const [isTreksDropdownOpen, setIsTreksDropdownOpen] = useState(false);
  const [isMobileTreksOpen, setIsMobileTreksOpen] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);
  const { isDarkMode } = useTheme();
  const pathname = usePathname();

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsTreksDropdownOpen(false);
      }
    };

    if (isTreksDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isTreksDropdownOpen]);

  const navItems = [
    { name: 'Home', href: '/' },
    { name: 'About', href: '/about' },
    { name: 'Adventures', href: '/treks', hasDropdown: true },
    { name: 'Services', href: '/services' },
    { name: 'Contact', href: '/contact' },
  ];

  const treksSubmenu = [
    { name: 'Treks', href: '/treks', icon: Mountain, description: 'Explore trekking adventures' },
    { name: 'Peak Expedition', href: '/peak-expedition', icon: Flag, description: 'Conquer mountain peaks' },
    { name: 'Safari Adventures', href: '/safari', icon: Binoculars, description: 'Wildlife and nature tours' },
  ];

  const isActive = (href: string) => {
    if (href === '/') return pathname === '/';
    return pathname.startsWith(href);
  };

  return (
    <>
      {/* Top Bar */}
      <div className={`${isDarkMode ? 'bg-gray-800' : 'bg-primary-500'} text-white py-2`}>
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center text-sm">
            <div className="flex gap-6">
              <a href="tel:+977" className="flex items-center gap-2 hover:text-gray-200 transition-colors">
                <Phone size={14} />
                <span className="hidden sm:inline">+977 98XXXXXXXX</span>
              </a>
              <a href="mailto:info@ngimalaya.com" className="flex items-center gap-2 hover:text-gray-200 transition-colors">
                <Mail size={14} />
                <span className="hidden sm:inline">info@ngimalaya.com</span>
              </a>
            </div>
            <ThemeToggle />
          </div>
        </div>
      </div>

      {/* Main Header */}
      <header className={`sticky top-0 z-50 transition-all duration-300 ${
        isScrolled 
          ? isDarkMode 
            ? 'bg-gray-900/95 backdrop-blur-md shadow-lg' 
            : 'bg-white/95 backdrop-blur-md shadow-lg'
          : isDarkMode 
            ? 'bg-gray-900' 
            : 'bg-white'
      }`}>
        <nav className="container mx-auto px-4">
          <div className="flex items-center justify-between h-20">
            {/* Logo */}
            <Link href="/" className="flex items-center">
              <img
                src={getImageSrc(isDarkMode ? lightImage : darkImage)}
                alt="Ngimalaya Adventure Nepal"
                className="h-12 md:h-14 w-auto"
              />
            </Link>

            {/* Desktop Navigation */}
            <div className="hidden lg:flex items-center space-x-8">
              {navItems.map((item) => (
                item.hasDropdown ? (
                  <div key={item.name} className="relative" ref={dropdownRef}>
                    <button
                      onClick={() => setIsTreksDropdownOpen(!isTreksDropdownOpen)}
                      className={`font-display font-semibold uppercase tracking-wider text-sm transition-colors duration-300 flex items-center gap-1 ${
                        isActive(item.href)
                          ? 'text-primary-500'
                          : isDarkMode
                            ? 'text-gray-300 hover:text-primary-500'
                            : 'text-gray-700 hover:text-primary-500'
                      }`}
                    >
                      {item.name}
                      <ChevronDown size={16} className={`transition-transform duration-300 ${isTreksDropdownOpen ? 'rotate-180' : ''}`} />
                    </button>

                    {/* Dropdown Menu */}
                    {isTreksDropdownOpen && (
                      <div className={`absolute top-full left-0 mt-2 w-64 rounded-lg shadow-xl border overflow-hidden z-50 ${
                        isDarkMode 
                          ? 'bg-gray-800 border-gray-700' 
                          : 'bg-white border-gray-200'
                      }`}>
                        {treksSubmenu.map((subItem) => {
                          const Icon = subItem.icon;
                          return (
                            <Link
                              key={subItem.name}
                              href={subItem.href}
                              onClick={() => setIsTreksDropdownOpen(false)}
                              className={`flex items-start gap-3 px-4 py-3 transition-all duration-300 ${
                                pathname === subItem.href
                                  ? isDarkMode
                                    ? 'bg-primary-600/20 text-primary-400'
                                    : 'bg-primary-50 text-primary-600'
                                  : isDarkMode
                                    ? 'hover:bg-gray-700 text-gray-300 hover:text-primary-400'
                                    : 'hover:bg-gray-50 text-gray-700 hover:text-primary-600'
                              }`}
                            >
                              <Icon size={20} className={`mt-0.5 flex-shrink-0 ${
                                pathname === subItem.href ? 'text-primary-500' : ''
                              }`} />
                              <div className="flex-1">
                                <div className="font-display font-semibold text-sm uppercase tracking-wide">
                                  {subItem.name}
                                </div>
                                <div className={`text-xs mt-0.5 ${
                                  isDarkMode ? 'text-gray-400' : 'text-gray-500'
                                }`}>
                                  {subItem.description}
                                </div>
                              </div>
                            </Link>
                          );
                        })}
                      </div>
                    )}
                  </div>
                ) : (
                  <Link
                    key={item.name}
                    href={item.href}
                    className={`font-display font-semibold uppercase tracking-wider text-sm transition-colors duration-300 ${
                      isActive(item.href)
                        ? 'text-primary-500'
                        : isDarkMode
                          ? 'text-gray-300 hover:text-primary-500'
                          : 'text-gray-700 hover:text-primary-500'
                    }`}
                  >
                    {item.name}
                  </Link>
                )
              ))}
              <button 
                onClick={() => setIsBookingModalOpen(true)}
                className="bg-primary-500 hover:bg-primary-600 text-white px-6 py-2.5 font-display font-bold uppercase tracking-wider text-sm transition-all duration-300 shadow-lg hover:shadow-xl"
              >
                Book Now
              </button>
            </div>

            {/* Mobile Menu Button */}
            <button
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className={`lg:hidden p-2 ${isDarkMode ? 'text-gray-300' : 'text-gray-700'}`}
            >
              {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>

          {/* Mobile Menu */}
          {isMobileMenuOpen && (
            <div className={`lg:hidden py-4 border-t ${
              isDarkMode ? 'border-gray-700 bg-gray-900' : 'border-gray-200 bg-white'
            }`}>
              {navItems.map((item) => (
                item.hasDropdown ? (
                  <div key={item.name}>
                    <button
                      onClick={() => setIsMobileTreksOpen(!isMobileTreksOpen)}
                      className={`w-full flex items-center justify-between py-3 px-4 font-display font-semibold uppercase tracking-wider text-sm transition-colors ${
                        isActive(item.href)
                          ? 'text-primary-500 bg-primary-50'
                          : isDarkMode
                            ? 'text-gray-300 hover:text-primary-500 hover:bg-gray-800'
                            : 'text-gray-700 hover:text-primary-500 hover:bg-gray-50'
                      }`}
                    >
                      {item.name}
                      <ChevronDown size={16} className={`transition-transform duration-300 ${isMobileTreksOpen ? 'rotate-180' : ''}`} />
                    </button>
                    
                    {/* Mobile Submenu */}
                    {isMobileTreksOpen && (
                      <div className={`${isDarkMode ? 'bg-gray-800' : 'bg-gray-50'}`}>
                        {treksSubmenu.map((subItem) => {
                          const Icon = subItem.icon;
                          return (
                            <Link
                              key={subItem.name}
                              href={subItem.href}
                              onClick={() => {
                                setIsMobileMenuOpen(false);
                                setIsMobileTreksOpen(false);
                              }}
                              className={`flex items-center gap-3 py-3 px-8 transition-colors ${
                                pathname === subItem.href
                                  ? 'text-primary-500'
                                  : isDarkMode
                                    ? 'text-gray-400 hover:text-primary-500'
                                    : 'text-gray-600 hover:text-primary-600'
                              }`}
                            >
                              <Icon size={18} className={`flex-shrink-0 ${
                                pathname === subItem.href ? 'text-primary-500' : ''
                              }`} />
                              <span className="font-display font-medium text-sm uppercase tracking-wide">
                                {subItem.name}
                              </span>
                            </Link>
                          );
                        })}
                      </div>
                    )}
                  </div>
                ) : (
                  <Link
                    key={item.name}
                    href={item.href}
                    onClick={() => setIsMobileMenuOpen(false)}
                    className={`block py-3 px-4 font-display font-semibold uppercase tracking-wider text-sm transition-colors ${
                      isActive(item.href)
                        ? 'text-primary-500 bg-primary-50'
                        : isDarkMode
                          ? 'text-gray-300 hover:text-primary-500 hover:bg-gray-800'
                          : 'text-gray-700 hover:text-primary-500 hover:bg-gray-50'
                    }`}
                  >
                    {item.name}
                  </Link>
                )
              ))}
              <div className="px-4 pt-3">
                <button 
                  onClick={() => {
                    setIsBookingModalOpen(true);
                    setIsMobileMenuOpen(false);
                  }}
                  className="w-full bg-primary-500 hover:bg-primary-600 text-white px-6 py-3 font-display font-bold uppercase tracking-wider text-sm transition-all duration-300 shadow-lg"
                >
                  Book Now
                </button>
              </div>
            </div>
          )}
        </nav>
      </header>

      {/* Booking Modal */}
      <BookingModal
        isOpen={isBookingModalOpen}
        onClose={() => setIsBookingModalOpen(false)}
      />
    </>
  );
};

export default Header;
